.btn-fill-out {
  background-color: transparent;
  border: 1px solid #ff2900;
  color: #fff;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn-fill-out::before,
.btn-fill-out::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: #ff2900;
  z-index: -1;
  transition: all 0.3s ease-in-out;
  width: 51%;
}

.btn-fill-out::after {
  right: 0;
  left: auto;
}

.btn-fill-out:hover:before,
.btn-fill-out:hover:after {
  width: 0;
}

.btn-fill-out:hover {
  color: #ff2900 !important;
}

.btn-fill-line {
  background-color: transparent;
  border: 1px solid #333;
  overflow: hidden;
  position: relative;
  color: #fff !important;
  transition: all 0.8s ease 0s;
  z-index: 1;
}

.btn-fill-line:before,
.btn-fill-line:after {
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  bottom: 0;
  background-color: #333;
  z-index: -1;
  transition: all 0.3s ease-in-out;
  width: 50%;
}

.btn-fill-line::after {
  right: 0;
  left: auto;
}

.btn-fill-line:hover:before,
.btn-fill-line:hover:after {
  width: 0;
}

.btn-fill-line:hover {
  color: #333 !important;
}

.btn-border-fill {
