.ripple {
  background-color: #ff2900;
  border-radius: 100%;
  color: #fff;
  position: relative;
  display: inline-block;
  margin-right: 25px;
  line-height: 50px;
  font-size: 22px !important;
  vertical-align: middle;
  margin-left: -15px;
  height: 50px;
  width: 50px;
  text-align: center;
}

.ripple i {
  margin-right: 0;
  margin-left: 3px;
  font-size: 22px;
}

.ripple::before,
.ripple::after {
  -webkit-animation: ripple 1.8s infinite;
  -moz-animation: ripple 1.8s infinite;
  -o-animation: ripple 1.8s infinite;
  animation: ripple 1.8s infinite;
  background-color: #ff2900;
  border-radius: 100%;
  margin: -20px;
  bottom: 0;
  content: "";
  display: block;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

.ripple::before {
  -webkit-animation-delay: 0.4s;
  -moz-animation-delay: 0.4s;
  -o-animation-delay: 0.4s;
  animation-delay: 0.4s;
}

.ripple::after {
  -webkit-animation-delay: 1s;
  -moz-animation-delay: 1s;
  -o-animation-delay: 1s;
  animation-delay: 1s;
}

@-webkit-keyframes ripple {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0)
  }

  50% {
    opacity: 0.5;
  }

  100% {
    opacity: 0;
    -webkit-transform: scale(1);
    transform: scale(1)
  }
}

@keyframes ripple {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0)
  }

  50% {
    opacity: 0.5;
  }

  100% {
    opacity: 0;
    -webkit-transform: scale(1);
    transform: scale(1)
  }
}

