.login-logo {
  width: 185px;
  margin: 0 auto;
  background: #cb150f;
  border-radius: 4px;
  margin-bottom: 10px;
}

.login_wrap {

  .padding_eight_all {
    padding: 5%;
  }

  .login-title {
    font-size: 32px;
    line-height: 40px;
    color: #121212;
    font-weight: 600;
    letter-spacing: -0.4px;
    margin-bottom: 0;
  }

  .login_continue {
    font-size: 16px;
    line-height: 24px;
    color: #121212;
    font-weight: 600;
    letter-spacing: -0.2px;
    margin-bottom: 0;
  }

  .different_login {
    margin: 15px 0;
  }
}

.css-zqtbdk {
  -moz-box-align: baseline;
  align-items: baseline;
  border-width: 0px;
  box-sizing: border-box;
  display: inline-flex;
  font-size: inherit;
  font-style: normal;
  max-width: 100%;
  outline: currentcolor none medium !important;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  border-radius: 3px;
  color: rgb(66, 82, 110) !important;
  cursor: default;
  padding: 0px 8px;
  transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  vertical-align: middle;
  width: 100%;
  height: 40px !important;
  line-height: 40px !important;
  background: rgb(255, 255, 255) none repeat scroll 0% 0% !important;
  box-shadow: rgba(0, 0, 0, 0.2) 1px 1px 5px 0px !important;
  font-weight: bold;
  cursor: pointer;
}

.css-zqtbdk:not(:last-child) {
  margin-bottom: 16px;
}

.css-zqtbdk div,
.css-zqtbdk span {
  display: flex !important;
  -moz-box-pack: center;
  justify-content: center;
  color: #424242;
}

.css-1vqao0l {
  align-self: center;
  display: inline-flex;
  flex-wrap: nowrap;
  max-width: 100%;
  position: relative;
  width: 100%;
  -moz-box-pack: center;
  justify-content: center;
}

.css-t5emrf {
  -moz-box-align: center;
  align-items: center;
  align-self: center;
  flex: 1 1 auto;
  margin: 0px 4px;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: opacity 0.3s ease 0s;
  opacity: 1;
}

.login-instruction {

  .form-note,
  p,
  a {
    font-size: 15px;
    line-height: 24px;
    color: #424242;
  }

  a {
    text-decoration: none !important;
    color: #cb150f !important;
    font-weight: 600;
  }

}