  background-color: #fff;
  position: relative;
  padding: 30px;
}

.compare_box .table {
  margin: 0;
  display: block;
}

.compare_box table tr td {
  vertical-align: middle;
  width: 27%;
}

.compare_box table tr td.product_name {
  text-transform: capitalize;
}

.compare_box table tr td.row_title {
  width: 270px;
  text-transform: uppercase;
  font-weight: 600;
}

.row_remove a,
.row_remove a:hover {
  color: #FF0000;
}

.in-stock {
  color: #388e3c;
}

.out-stock {
  color: #dc3545;
}

.product_name a {
  font-weight: 600;
}

.row_color .product_color_switch span {
  pointer-events: none;
}

.product-thumbnail img {
  max-width: 100px;
}

.shop_cart_table th,
.shop_cart_table td,
.wishlist_table th,
.wishlist_table td {
  vertical-align: middle;
  text-align: center;
}

.shop_cart_table th.product-name,
.shop_cart_table td.product-name,
.wishlist_table th.product-name,
.wishlist_table td.product-name {
  text-align: left;
  text-transform: capitalize;
}



.product-remove a i {
  height: 30px;
  width: 30px;
  color: #292b2c;
  display: inline-block;
  line-height: 30px;
  text-align: center;
  border-radius: 100%;
  font-size: 14px;
}

.product-remove a i:hover {
  background-color: #F00;
  color: #fff;
}

.shop_cart_table td.product-price,
.shop_cart_table td.product-subtotal {
  font-weight: 600;
}

.shop_cart_table .table {
  margin: 0;
}

.toggle_info {
  padding: 20px;
  background-color: #F7F7F7;
}

.toggle_info span {
  padding-left: 25px;
  display: inline-block;
  width: 100%;
  position: relative;
}

.toggle_info i {
  margin-right: 10px;
  color: #ff2900;
  position: absolute;
  left: 0;
  top: 5px;
}

.login_form .panel-body,
.coupon_form .panel-body {
  border: 1px solid #ddd;
  padding: 30px;
  margin-top: 30px;
}

label.label_info {
  color: #292b2c !important;
  font-weight: 600;
}

.ship_detail {
  margin-bottom: 10px;
  display: inline-block;
  width: 100%;
}

.product-qty {
  font-weight: 600;
}

.product-subtotal {
  font-weight: 600;
}

.order_review {
  background-color: #f7f8fb;
  padding: 30px;
}

.order_table thead th {
  border-bottom-width: 1px;
  font-weight: 600;
}

.order_table tfoot th {
  font-weight: 600;
}

.order_table tbody td {
  border: 0;
}

.payment_method {
  margin-bottom: 30px;
  display: inline-block;
  width: 100%;
}

.payment_option .custome-radio {
  margin-bottom: 10px;
}

.payment-text {
  display: none;
  margin-top: 5px;
  margin-bottom: 0;
}

.payment_option .custome-radio:first-child .payment-text {
  display: block;
}

.payment_option .custome-radio .form-check-label {
  color: #292b2c;
  font-weight: 600;
}

.wishlist_table .table td {
  border-bottom: 1px solid #dee2e6;
}

.wishlist_table .badge {
  line-height: normal;
  vertical-align: middle;
  padding: 2px 10px;
}

#load-more {
  transition: none;
}

.grid_item_hide .product,
.grid_item_hide .blog_post {
  -moz-transform: scale(0);
  -webkit-transform: scale(0);
  transform: scale(0);
}

.grid_item_hide {
  opacity: 0;
  visibility: hidden;
  height: 0;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  transform: scale(0);
}

.loading {
  background: url('./img/loading.gif') center center no-repeat;
  font-size: 0 !important;
  background-color: #000 !important;
  border: 0;
  height: 51px;
  width: 50px;
  padding: 0;
}

.loading::after,
.loading::before {
  content: normal;
}

.load_more_wrap {
  margin-top: 20px;
}

.load_more_wrap span {
  display: inline-block;
  margin: 0;
  line-height: 1;
  font-size: 14px;
}

.order_complete i {
  color: #ff2900;
  font-size: 80px;
  margin-bottom: 20px;
}

.empty_icon {
  background-color: #DFFFED;
  display: inline-block;
  height: 100px;
  width: 100px;
  line-height: 100px;
  border-radius: 100%;
  margin-bottom: 25px;
}

.card-body .comments {
  margin-top: 5px;
}

